.mapouter{position:relative;text-align:right;height:500px;width:600px; max-width: 100%;}
.gmap_canvas {overflow:hidden;background:none!important;height:500px;width:600px; max-width: 100%;}
*{
  margin: 0 auto;
  box-sizing: border-box;
  text-align: center;
  font-family: Montserrat ;
}
.white{
font-weight: 200;
  color: white;
}
.arow{
  width: 2rem;
}
.cordon2{
  padding: 1rem 0;
  display: flex;
  align-items: center;
}
.cordon2 a img{
  width: 25%;
  border-radius: 25%;
}
.seebtn{
  width: min-content;
  font-size: 1.5rem;
  text-decoration: none;
  border: none;
  font-weight: 500;
  padding: 1rem 2rem;
  border-radius: 20% 20%;
  background-color: rgba(255, 255, 255, 0.911);
}
.seebtn a{
  text-decoration: none;
  color: black;
  font-weight: bold;
  letter-spacing: 4px;
}
.seebtn a:visited,.seebtn a:active{
  text-decoration: none;
}
.seebtn:hover .arow{
  transform: translateY(10px);
}
.col1{
  text-align: left;
  padding-top:.5rem
}
.grid h3{
  font-weight: 400;
}
.underlined{
  position: relative;
  margin-bottom: 1rem;
  width: max-content;
}

a, button{
  cursor: pointer;
}
.underlined::after{
  position: absolute;
  bottom: -3px;
  left: 0;
  content: '';
  width: 100%;
  height: 5px;
  background-color: blueviolet;
}
.mainContainer{
  height: 90vh;
  color: wheat;
  background-image: url('./assets/bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;

}
.mainContainer .col2 a:hover {
  cursor: pointer;
  text-decoration: underline wheat;
}

.navBar{
  display: flex;
  align-items: center;
}

.mainText{
  padding: 10rem 1rem;
}
.weight300{
  font-weight: 500;
}
.navList{
  color: black;
  background-color: rgba(245, 222, 179, 0.815);
  flex-direction: column;
  position: absolute;
  top: .5rem;
  right: .5rem;
  height:60vh;
  font-size: 1.5rem;
  padding-bottom: 1rem;
}
.exitMenuImage, .menuImage{
  filter: invert();
  padding-top: 10px;
  height: 40px;
  cursor: pointer;
}
.pfbtn ,.pfbtn:visited{
  color: black;
  text-decoration: none;

}
.briefContainer{
  background-image: url('./assets/img.jpg');
  background-size: cover;
}
.cart{
  background-color: rgba(240, 255, 255, 0.726);
  padding: 5% 10% 15% 10% ;
  border-radius: 5%;
  box-shadow : 7px 7px rgba(0, 0, 0, 0.158);
}
.myImage{
  width: 100%;
  border: solid gray;
  border-radius: 10%;
}
.btn{
  margin-top: .5rem;
  margin-right: .5rem;
  padding: .5rem 1rem;
  border: 1px solid gray;
  border-radius: 10%;
}
.colm1{
  margin-bottom: .5rem;
}
.skillContainer{
  background-image: url(./assets/img2.png);
  background-size: cover;
}
.skillImage img{
  width: 50%;
  border-radius: 50%;
  border: solid gray;
}
.adobe img{
  height:7.5rem;
  width:7.5rem;
  border-radius: 50%;

  border: solid gray;
}
.portfolioContainer{
  background-image: url(./assets//img3.jpg);
  background-size: cover;
}
.projectTitle{
  font-weight: 600;

}
.projectImage img{
  width: 100%;
  border-radius: 10%;
  border: solid 1px gray;
}
.projetContainer {
  margin-bottom: .5rem;
}
.contactContainer{
  background-image: url(./assets/img4.jpg);
  background-size: cover;
}
 
.contactContainer input, .contactContainer textarea {
  display: block;
  width: 80%;
}
.contactContainer > h3{
  color: white;
}
.subCart{
  height: 100%;
  margin: .5rem 0;
  background-color: white;
  padding: 2rem;
}

.subCart img{
  width: 100%;
}
.subCart button{
  margin-top: .5rem;
  padding: 1rem 2rem;
  background-color: rgb(86, 189, 253);
  border: none;
  font-weight: bold;
  border-radius: 25% ;
  border: solid 1PX gray;
}
.cordon{
  margin: .5rem 0;
  display: flex;
  justify-content: center;
}
.cordon img{
  display: inline;
  width: 25px;
}
.cordon p {
  display: inline;
}
.grayColor{
  color: rgb(48, 48, 48);
  font-weight: 300;
}
@media(min-width : 700px){
  .seebtn{
  width: max-content;
  }
  .menuImage, .exitMenuImage{
    display: none;
  }
  .navBar{
    font-size: 1.2rem;
  }
  .col1{
    position: relative;
    left: -15rem;
  }
  .col1 h2{
    font-weight: 500;

  }
  .navList a:visited,.navList a:active,.navList a{
    color: wheat;
    text-decoration: none
  }
  .sendMsg{
    padding: 8rem 0;
    font-size: 150%;
  }
  .mapouter, #mapouter{
    width: 100%;
  }
  .gmap_canvas, #gmap_canvas{
    width: 100%;
  }
  .mainText h1{
    font-size: 3rem;
  }
  .mainText h2{
    font-size: 2rem;
  }
  .navList{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    height: min-content;
    background: none;
    color: wheat;
  }
  .flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .flex > div{
    margin: 0 .5rem;
    width : 100%;
    height: 100%;
  }
  .flex .colm1{
    width: 100%;
  }
  .flex .colm2{
    font-size: 1.2rem;
  }
  .colm2 h3{
    font-weight: 400;
  }
  .grid{
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2,1fr);
  }


}
@media(min-width : 900px){
  .grid{
    grid-template-columns: repeat(3,1fr);
    
  }
}
@media(min-width : 1100px){
  .grid{
    grid-template-columns: repeat(4,1fr);
  }
}
.container{
  padding: 1rem ;
}
@media (max-width : 700px){
  .mainContainer{
    height: 80vh;
  }
  .navList{
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    justify-content: space-around;
    z-index: 5;
    height: 60vh;
    
  }
  .navList a {
    text-decoration: none;
  }
  .blackk{
    color: black;
  }
  .hide{
  display: none;
}
}
